/* =====  Button ===== */
button:focus {
  box-shadow: none !important;
}

button.disabled, button:disabled {
  opacity: 0.65;
}

.btn-primary {
  text-transform: capitalize;
  padding: 9px 11px;
  background-color: #1d82f5 !important;
  color: #fff !important;
  border: 1px solid #1d82f5 !important;
  position: relative;
}
.btn-primary:hover {
  background-color: black !important;
  border: 1px solid black !important;
  color: #fff !important;
}

.btn-primary.disabled, .btn-primary:disabled {
  background-color: #1d82f5;
}
.btn-primary.disabled:hover, .btn-primary:disabled:hover {
  background-color: #1d82f5 !important;
  border: 1px solid #1d82f5 !important;
  cursor: not-allowed;
}

.btn-secondary {
  text-transform: capitalize;
  padding: 9px 11px;
  border: solid 1px #616e80;
  background-color: #fff !important;
  color: #616e80 !important;
  position: relative;
}
.btn-secondary:hover {
  background-color: black !important;
  border: solid 1px #000 !important;
  color: #fff !important;
}

.btn-secondary.disabled:hover, .btn-secondary:disabled:hover {
  border: solid 1px #616e80 !important;
  background-color: #fff !important;
  color: #616e80 !important;
  cursor: not-allowed;
}

.btn-cancel {
  text-transform: capitalize;
  padding: 9px 11px;
  border: 1px solid #fff;
  background-color: #fff !important;
  color: #99A5B5 !important;
  position: relative;
}
.btn-cancel:hover {
  background-color: black !important;
  border: solid 1px #000 !important;
  color: #fff !important;
}

.btn-cancel.disabled:hover, .btn-cancel:disabled:hover {
  border: 1px solid #fff !important;
  background-color: #fff !important;
  color: #99A5B5 !important;
  cursor: not-allowed;
}

.btn-danger {
  text-transform: capitalize;
  background-color: #D30000;
  color: #fff;
  border: 0px;
  position: relative;
  padding: 9px 11px;
