.w-tables {
  box-shadow: 0 0 4px 0 #d2d9e4;
}
@media (max-width: 767.98px) {
  .w-tables {
    overflow-x: scroll;
  }
}

.dataTables_wrapper {
  width: 100%;
}

.dataTables_filter {
  display: none;
}

.table thead th, .table th, .table td {
  border: 0px;
  padding: 6px;
}

.table thead th {
  font-size: 13px;
  font-weight: 500;
  color: #99A5B5;
  box-shadow: 0 1px 0 0 #f1f1f3;
}

.table thead th:first-child, .table tbody td:first-child {
  padding-left: 20px;
}

.table tr td {
  font-size: 13px;
  color: #28313c;
  box-shadow: 0 1px 0 0 #f1f1f3;
  vertical-align: middle;
}

.taskEmployeeImg {
  width: 25px;
  height: 25px;
  display: inline-block;
  overflow: hidden;
  object-fit: cover;
}

.task_view {
  border: 1px solid #99A5B5;
  border-radius: 4px;
  display: inline-flex;
}
.task_view .taskView {
  padding: 0px 7px;
  line-height: 2;
  color: #99A5B5;
  border-right: 1px solid #99A5B5;
}
.task_view .taskView:hover {
  background-color: #F2F4F7;
}
.task_view .task_view_more {
  color: #99A5B5;
  padding: 6.3px 5px;
  border-radius: 4px;
}
.task_view .task_view_more:hover {
  background-color: #F2F4F7;
}
.task_view .task_view_more:after {
  content: none;
}
.task_view .dropdown-menu {
  border: 0;
  -webkit-box-shadow: 1px 4px 6px 4px rgba(104, 104, 104, 0.0784313725);
  box-shadow: 1px 4px 6px 4px rgba(104, 104, 104, 0.0784313725);
}

table.dataTable {
  margin-bottom: 0px !important;
}
@media (max-width: 767.98px) {
  table.dataTable {
    width: 862px;
  }
}

.dataTables_paginate {
  margin: 12px !important;
}

.dataTables_length {
  padding: 15px 20px;
}

.dataTables_length select {
  height: 35px;
  min-width: 53px;
}

.pagination .page-item.active .page-link {
  background-color: #1d82f5;
}

.pagination li {
  margin-right: 8px;
}
.pagination li a {
  border-radius: 4px;
  min-width: 35px;
  height: 35px;
  line-height: 1.54;
  font-size: 13px;
  color: #4d4f5c;
  border: 1px solid #e8eef3;
  display: flex;
  align-items: center;
  justify-content: center;
}
.pagination li a:focus {
  box-shadow: none;
}

.page-item.active .page-link {
  background-color: #1d82f5;
  border-color: #1d82f5;
}
.page-item.active .page-link:hover, .page-item.active .page-link:focus {
  color: #fff !important;
  box-shadow: none;
}

div.dataTables_wrapper div.dataTables_info {
  padding-top: 1.3rem;
  padding-left: 1rem;
}

.page-item.active .page-link {
  z-index: 0;
}

.filter-box {
  box-shadow: 0 1px 0 0 #e8eef3;
  width: 100%;
  top: 63px;
  position: sticky;
  z-index: 1;
}
@media (max-width: 991.98px) {
  .filter-box {
    width: 100%;
    position: initial;
  }
}

.more-filters {
  border-left: solid 1px #e8eef3;
}
@media (max-width: 991.98px) {
  .more-filters {
    border-left: 0px;
  }
}
.more-filters a {
  line-height: 2;
}
.more-filters .more-filter-tab {
  background-color: #fff;
  position: fixed;
  top: 62px;
  right: 0;
  z-index: 9;
  border: solid 1px #e8eef3;
  white-space: nowrap;
  overflow-x: hidden;
  transform: translate3d(273px, 0, 0);
  height: 100%;
  width: 268px;
  transition: all ease-in 0.3s;
}
.more-filters .more-filter-tab .clear-all {
  transition: all ease 0.3s;
  position: fixed;
  width: 100% !important;
}
.more-filters .more-filter-tab h3 {
  line-height: 1.17;
  padding: 18px 28px 0;
}
.more-filters .more-filter-tab label {
  line-height: 1.14;
  padding: 0px 28px;
  margin-bottom: 12px;
}
.more-filters .more-filter-tab.in {
  transform: translate3d(0px, 0, 0);
  transition: all ease-in 0.3s;
  width: 268px;
}
.more-filters .more-filter-tab.in .filter-detail {
  height: calc(100vh - 197px);
  overflow-y: auto;
  overflow-x: hidden;
}
.more-filters .more-filter-tab.in .clear-all {
  transition: width ease 0.3s;
  padding: 0px 28px;
  border-top: solid 1px #e8eef3;
}
.more-filters .more-filter-tab.in .clear-all button {
  padding: 9px 11px;
  margin-right: 9px;
}
.more-filters .filter-detail {
  height: calc(100vh - 197px);
}

.close-more-filter {
  position: absolute;
  right: 28px;
  top: 14px;
}
.close-more-filter:hover {
  color: #000;
}

.select-filter {
  padding: 0px 28px;
}